#start-button {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    width: 260px;
    height: 70px;
    /* выравнивание по вертикали, хак. должно быть эквивалентно height элемента */
    /* line-height:70px; */
    color: #333;
    background: rgb(221, 221, 221);
    font-weight: 700;
    font-size: 2em;
    text-align: center;
    border-radius: 3px;
    cursor: pointer;
    -webkit-user-select: none; /* Safari */        
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* IE10+/Edge */
    user-select: none; /* Standard */
    text-transform: capitalize;
    margin: 30px auto;
}

.none {
    visibility: hidden;
}


