
#host-buttons-block {
    text-align: center;
    /* background-color: rgb(3, 119, 119); */
    background-color:  #003366;
    
    /* display: flex;
    flex-direction: row;
    justify-content: space-around;  */
    /* width: 72%; */
    width: 100%;
    /* margin: 0 auto; */
    padding: 50px 0;
    /* margin-top: 500px; */

    -webkit-animation: arriving 1s cubic-bezier(0.075, 0.82, 0.165, 1) 1 forwards;

            animation: arriving 1s cubic-bezier(0.075, 0.82, 0.165, 1) 1 forwards;
}

#host-buttons-block-header {
    color: white;
    font-size: 2em;
    margin-bottom: 40px;
    padding: 0 2%;
}

#host-buttons-row {
    /* background-color: rgb(3, 119, 119); */
    display: -ms-flexbox;
    display: flex;
    /* change for mobile to column */
    -ms-flex-direction: column;
        flex-direction: column;
    /* justify-content: center; */
    -ms-flex-align: center;
        align-items: center;
    /* change for mobile */
    padding: 0 5%;
    /* width: 72%;
    margin: 0 auto;
    padding: 10px; */
}

.host-button-item {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    /* change for mobile */
    width: 90%;
    margin-bottom: 30px;
}

.host-button-item-description {
    color: white;
    padding-top: 10px;
    text-align: center;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 21px;
}

@media all and (min-width: 768px) {
    #host-buttons-row {
        /* background-color: rgb(3, 119, 119); */
        /* display: flex; */
        -ms-flex-direction: row;
            flex-direction: row;
        /* flex-wrap: wrap; */
        -ms-flex-pack: distribute;
            justify-content: space-around;
        -ms-flex-align: start;
            align-items: flex-start;
        padding: 0 5%;
        
    }

    .host-button-item {
        /* change for mobile */
        /* width: 23%;  */
        margin-bottom: 20px;
    }

}

/* Animation */

@-webkit-keyframes arriving {
    from { margin-top: 700px; opacity: .1 }
    to { margin-top: 300px; opacity: 1 }
}

@keyframes arriving {
    from { margin-top: 700px; opacity: .1 }
    to { margin-top: 300px; opacity: 1 }
}
